import uParse from '@/components/u-parse/u-parse.vue'
export default {
	components: {
	    uParse
	},
	data() {
		return {
			resemble:[],
			zh_explain:'',
			word:'',
			word_change:'',
			word_resemblance:'',
			soundmark:'',
			phrase_resemblance:'',
			en_explain:''
		};
	},
	onLoad() {
		this.get_word_lib()
		
	},
	methods:{
		get_word_lib:function(){
			let data = {
				user_token:this.$store.getters.common_token,
				app_class:'wechat',
				word:'abandon'
			}
			let _this = this
			this.$api.word_lib(data).then(res=>{
				console.log(res)
				
				this.word = res.data.content.result.word
				this.soundmark = res.data.content.result.soundmark
				this.zh_explain = res.data.content.result.zh_explain
				this.en_explain = res.data.content.result.en_explain
				this.word_change = res.data.content.result.word_change
				this.word_resemblance = res.data.content.result.word_resemblance
				this.phrase_resemblance = res.data.content.result.phrase_resemblance
			})
			
		}
	}
}
